* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, segoe ui, Roboto, Helvetica, Arial, sans-serif, apple color emoji, segoe ui emoji, segoe ui symbol;
  font-size: 100%;
  color: #000000;
  text-rendering: optimizeLegibility; }

main {
  margin: 2rem; }

:first-child {
  margin-top: 0; }

:last-child {
  margin-bottom: 0; }

h1, .h1 {
  font-size: 3.375rem;
  line-height: 1.1;
  margin-top: 1.6785em;
  margin-bottom: 0.3em; }

h2, .h2 {
  font-size: 2.25rem;
  line-height: 1.2;
  margin-top: 0.9em;
  margin-bottom: 0.45em; }

h3, .h3 {
  font-size: 1.5rem;
  line-height: 1.5;
  margin-top: 1.33em;
  margin-bottom: 0.66em; }

h4, .h4 {
  font-size: 1.25rem;
  line-height: 1.1;
  margin-top: 1.2em;
  margin-bottom: 0.8em; }

h5, .h5 {
  font-size: 1.1rem;
  line-height: 1.1;
  margin-top: 1.125em;
  margin-bottom: 0.9em; }

h6, .h6 {
  font-size: 1rem;
  line-height: 1;
  margin-top: 1.5em;
  margin-bottom: 0.5em; }

html {
  font-feature-settings: "calt","liga","hist","onum","pnum"; }

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  font-weight: normal; }

a {
  color: inherit; }

p {
  margin-bottom: 1rem; }

hr {
  border: none;
  height: 0.1875rem;
  background: #000000; }

.sr__text,
.sr__input,
.sr__label {
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px); }

button, input, textarea {
  transition: background-color .1s linear, border-color .1s linear, color .1s linear, box-shadow .1s linear, transform .1s ease; }

button, input[type=button], input[type=checkbox], input[type=submit] {
  cursor: pointer; }

input:not([type=checkbox]):not([type=radio]), select {
  display: block; }

button, input, select, textarea {
  color: #000;
  background-color: #efefef;
  font-family: inherit;
  font-size: inherit;
  margin-right: 6px;
  margin-bottom: 6px;
  padding: 10px;
  border: none;
  border-radius: 6px;
  outline: none; }

button, input:not([type=checkbox]):not([type=radio]), select, textarea {
  -webkit-appearance: none; }

textarea {
  margin-right: 0;
  width: 100%;
  box-sizing: border-box;
  resize: vertical; }

button, input[type=button], input[type=submit] {
  padding-right: 30px;
  padding-left: 30px; }

button:hover, input[type=button]:hover, input[type=submit]:hover {
  background: #ddd; }

button {
  color: white;
  background: #FE4343; }
  button:hover {
    background: #cc0000; }

button:focus, input:focus, select:focus, textarea:focus {
  box-shadow: 0 0 0 2px rgba(0, 150, 191, 0.67); }

button:active, input[type=button]:active, input[type=checkbox]:active, input[type=radio]:active, input[type=submit]:active {
  transform: translateY(2px); }

button:disabled, input:disabled, select:disabled, textarea:disabled {
  cursor: not-allowed;
  opacity: .5; }

.center {
  width: 100%;
  margin-left: auto;
  margin-right: auto; }

.center-text {
  text-align: center; }

.flex-row {
  display: flex;
  flex-direction: row; }

.flex-column {
  display: flex;
  flex-direction: column; }

.margin-s {
  margin: 1rem; }

.margin-m {
  margin: 2rem; }

.margin-l {
  margin: 4rem; }

.margin-t-s {
  margin-top: 1rem; }

.margin-t-m {
  margin-top: 2rem; }

.margin-t-l {
  margin-top: 4rem; }

.margin-b-s {
  margin-bottom: 1rem; }

.margin-b-m {
  margin-bottom: 2rem; }

.margin-b-l {
  margin-bottom: 4rem; }

.margin-l-s {
  margin-left: 1rem; }

.margin-l-m {
  margin-left: 2rem; }

.margin-l-l {
  margin-left: 4rem; }

.margin-r-s {
  margin-right: 1rem; }

.margin-r-m {
  margin-right: 2rem; }

.margin-r-l {
  margin-right: 4rem; }

header {
  margin: 0;
  padding: 2rem 1rem 2rem 1rem; }

h1, h2, h3 {
  text-align: center; }

h1, h2 {
  font-weight: bold; }

h1 {
  font-size: 1.5rem; }
  @media all and (min-width: 540px) {
    h1 {
      font-size: 3rem; } }

h2 {
  font-size: 1.2rem;
  font-weight: normal;
  max-width: 512px;
  margin-left: auto;
  margin-right: auto; }
  @media all and (min-width: 540px) {
    h2 {
      font-size: 2rem;
      font-weight: bold; } }
  @media all and (min-width: 820px) {
    h2 {
      max-width: none; } }

h3 {
  font-style: italic; }

main {
  max-width: 50rem;
  margin: 0 auto 1rem;
  padding: 1rem;
  overflow-x: hidden; }

svg#header-image {
  transform: scale(1.4);
  margin-bottom: 1rem; }
  svg#header-image #labels {
    display: none; }
  @media all and (min-width: 540px) {
    svg#header-image {
      transform: scale(1); }
      svg#header-image #labels {
        display: block; } }
  svg#header-image text {
    font-family: "Comic Sans MS", sans-serif;
    font-size: 64%; }

.sections {
  display: grid;
  grid-template-columns: 1fr;
  grid-column-gap: 2rem; }
  @media all and (min-width: 540px) {
    .sections {
      grid-template-columns: 1fr 1fr 1fr; } }

section {
  text-align: center;
  margin-bottom: 2rem; }

section a, a.button {
  display: block;
  max-width: 200px;
  margin: 0 auto;
  border-radius: 1rem;
  background: #FE4343;
  color: white;
  text-decoration: none;
  text-align: center;
  padding: 0.25rem 0 0.35rem 0; }
  section a:hover, a.button:hover {
    background: #cc0000; }

img {
  display: block;
  margin: 0 auto; }

strong {
  color: #FE4343; }

form {
  max-width: 30rem;
  margin: 0 auto;
  display: block; }

label {
  display: block;
  font-weight: bold;
  margin-top: 0.5rem;
  padding: 0.25rem 0 0.35rem 0; }

input[type="text"], input[type="email"] {
  width: 280px; }

footer {
  padding: 1rem;
  font-size: 80%;
  text-align: center; }
